* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.search-form {
  width: 600px;
  margin: 0 auto 40px;
  display: flex;
}

input {
  height: 40px;
  border: 1px solid #303030;
  flex-grow: 1;
  padding-left: 15px;
  font-size: 18px;
}

button, .load-more {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #2ecc71;
  border: none;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 14px;
  transition: background-color .25s linear;
}

.load-more {
  margin: 0 auto;
  display: block;
}

.load-more.is-hidden {
  display: none;
}

button:hover, button:focus, .load-more:hover, .load-more:focus {
  background-color: #27ae60;
}

.gallery {
  flex-wrap: wrap;
  margin-top: -30px;
  margin-bottom: 20px;
  margin-left: -30px;
  display: flex;
}

.gallery__item {
  opacity: 0;
  flex-basis: calc(25% - 30px);
  margin-top: 30px;
  margin-left: 30px;
  transition: opacity .5s linear, transform .5s linear;
  transform: scale(0);
}

.gallery__item.appear {
  opacity: 1;
  transform: scale(1);
}

.gallery-img {
  width: 100%;
  height: 100%;
  max-width: unset;
  object-fit: cover;
}

